.weather-display {
  padding: 40px;
  width: 55vw; }
  .weather-display h1 {
    border-bottom: 2px solid #0F1A20;
    color: #fff;
    font-size: 40px;
    text-shadow: 0 0 5px 0 #000;
    position: relative;
    margin-top: 40px;
    padding-bottom: 5px; }
    .weather-display h1:after {
      content: attr(name);
      font-size: 4em;
      line-height: 0.8em;
      opacity: 0.1;
      position: absolute;
      bottom: 5px;
      left: 40px; }

.weather-row {
  padding: 0 20px;
  margin: 40px 0; }
  .weather-row h2 {
    border-bottom: 2px solid #0F1A20;
    color: #F0EEEB;
    margin-top: 20px;
    padding-bottom: 5px;
    padding-right: 80px;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content; }
  .weather-row main {
    margin: 20px 0;
    display: flex;
    width: 100%;
    overflow-x: scroll; }

.weather-item {
  background-color: #FFFFFF;
  border-radius: 20px;
  text-align: center;
  padding: 20px;
  margin-right: 10px;
  min-width: 125px; }
  .weather-item p {
    text-transform: uppercase; }
    .weather-item p:first-child {
      padding-bottom: 5px;
      border-bottom: 1px solid #000;
      margin-bottom: 10px; }

.number {
  font-size: 48px;
  margin-bottom: 10px;
  color: #0D324D; }
  .number:after {
    font-size: 28px;
    font-weight: 400; }

.temp:after {
  content: "\00B0 F"; }

.wind:after {
  content: "mph";
  text-transform: lowercase; }

.humidity:after {
  content: "%";
  text-transform: lowercase; }

.visibility:after {
  content: "mi";
  text-transform: lowercase; }

.weather-item__desc,
.header-day {
  display: none; }

.loading {
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  top: 42vh;
  left: 42vw;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content; }
  .loading div {
    position: absolute;
    z-index: 1;
    background-color: #0f1a20;
    border-radius: 60%;
    width: 160px;
    height: 115px; }

.cloud {
  width: 180px;
  position: absolute;
  z-index: 10;
  fill: #017E91;
  -webkit-animation: colors 5s linear infinite;
          animation: colors 5s linear infinite; }

.sun {
  width: 100px;
  position: absolute;
  bottom: -10px;
  left: 0;
  z-index: -2;
  fill: #E8CF5F;
  -webkit-animation: rotator 7s linear infinite;
          animation: rotator 7s linear infinite; }

@-webkit-keyframes rotator {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@keyframes rotator {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg); }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg); } }

@-webkit-keyframes colors {
  0% {
    fill: #017E91; }
  25% {
    fill: #C7F9F0; }
  50% {
    fill: #017E91; }
  75% {
    fill: #004A68; }
  100% {
    fill: #017E91; } }

@keyframes colors {
  0% {
    fill: #017E91; }
  25% {
    fill: #C7F9F0; }
  50% {
    fill: #017E91; }
  75% {
    fill: #004A68; }
  100% {
    fill: #017E91; } }


